@import url(https://fonts.googleapis.com/css?family=Poppins:400,600,700|Work+Sans:400,500);

:root {
  --primaryColor: #5fb7ea;
  --primaryRGBA: rgba(0, 0, 0, 0.3);
  --mainGrey: #fafafa;
  --darkGrey: #5f5e5e;
  --mainWhite: #fff;
  --mainBlack: #222;
  --mainTransition: all 0.3s ease-in-out;
  --mainSpacing: 0.3rem; }

.text-title {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  letter-spacing: var(var(--mainSpacing));
  text-transform: capitalize; }

.text-main {
  color: #5fb7ea;
  color: var(--primaryColor); }

body {
  font-family: "Work Sans", sans-serif !important;
  background: #fafafa !important;
  background: var(--mainGrey) !important;
  color: #222;
  color: var(--mainBlack); }

.main-link {
  padding: 0.5rem 1rem;
  background: #5fb7ea;
  background: var(--primaryColor);
  color: #fff;
  color: var(--mainWhite);
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  letter-spacing: var(--mainSpacing);
  outline: 2px solid #5fb7ea;
  outline: 2px solid var(--primaryColor);
  outline-offset: 4px;
  border: none;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-transition: var(--mainTransition);
  transition: var(--mainTransition); }

.main-link:hover {
  background: #5f5e5e;
  background: var(--darkGrey);
  text-decoration: none;
  color: #5fb7ea;
  color: var(--primaryColor);
  outline-color: #5f5e5e;
  outline-color: var(--darkGrey); }

.cart-icon:hover {
  cursor: pointer; }

